declare global {
  interface Window {
    LoginWrapperConfig: {
      mountPointId: string;
      succeded: (token: string, ssoRedirectUrl: string)  => void;
      failed: ()  => void;
    };
  }
}

const LoginWrapperConfigDefaults = {
  mountPointId: 'login_app',
  succeded: (token: string, ssoRedirectUrl: string)  => {
    location.reload();
  },
  failed: () => {}
};

const config = window.LoginWrapperConfig || LoginWrapperConfigDefaults;
config.mountPointId = config.mountPointId || LoginWrapperConfigDefaults.mountPointId;
config.succeded = config.succeded || LoginWrapperConfigDefaults.succeded;
config.failed = config.failed || LoginWrapperConfigDefaults.failed;

export default config;
